import {useSelector} from "react-redux";
import {useEffect, useState} from "react";
import {
    BottomNavigation,
    BottomNavigationAction,
    Paper,
} from "@mui/material";
import DivvyLoading from "../components/DivvyLoading";
import BarChartIcon from '@mui/icons-material/BarChart';
import ListIcon from '@mui/icons-material/List';
import ProjectList from "../components/ProjectList";
import DivvyHomePage from "../components/DivvyHomePage";

export default function HomePage() {

    const {loaded} = useSelector(state => state.divvy);
    const [navValue, setNavValue] = useState(0)
    const [open, setOpen] = useState(true);

    useEffect(() => {
        if (loaded) {
            setOpen(false)
        }
    }, [loaded])

    return <>
        <DivvyLoading open={open} label="Preparing Awesome Sauce..."/>

        {
            navValue === 1 && <DivvyHomePage />
        }

        {
            navValue === 0 && <ProjectList />
        }

        <Paper sx={{position: 'fixed', bottom: 0, left: 0, right: 0}} elevation={3}>
            <BottomNavigation
                showLabels
                value={navValue}
                onChange={(event, newValue) => {
                    setNavValue(newValue);
                }}
            >
                <BottomNavigationAction label="Projects" icon={<ListIcon/>}/>
                <BottomNavigationAction label="Stats" icon={<BarChartIcon/>}/>
            </BottomNavigation>
        </Paper>
    </>
}